export default {
	data() {
		return {
			btnIsDisable: true,
			email: "",
			pwd: "",
			loading: false
		}
	},
	watch: {
		email(val) {
			if (this.inputIsEmpty()) {
				this.btnIsDisable = true
			} else {
				this.btnIsDisable = false
			}
		},
		pwd(val) {
			if (this.inputIsEmpty()) {
				this.btnIsDisable = true
			} else {
				this.btnIsDisable = false
			}
		}
	},
	methods: {
		handleConfirm() {
			let ePattern = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/
			if (!ePattern.test(this.email)) {
				uni.showToast({
					title: "邮箱格式不正确"
				})
				return;
			}
			if (!this.inputIsEmpty()) {
				this.loading = true
				this.btnIsDisable = true
				uni.showToast({
					title: "修改成功"
				})
			}
		},
		inputIsEmpty() {

			if (this.email && this.email != null && this.pwd && this.pwd != null) {
				return false;
			}
			return true;
		}
	}
}
